<template>
  <div id="app">
    <div class="nav" v-if="$route.path != '/manage/login'">
      <h2 class="logo">后台管理系统</h2>
      <Menu
        class="menu"
        mode="horizontal"
        theme="light"
        :active-name="selectMenu"
        @on-select="changeMenu"
      >
        <MenuItem name="公号托管">
          <Icon type="ios-paper"/>公号管理
        </MenuItem>
        <MenuItem name="消息管理">
          <Icon type="ios-stats"/>客服消息
        </MenuItem>
        <MenuItem name="群发列表">
          <Icon type="ios-stats"/>群发消息
        </MenuItem>
      </Menu>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectMenu: this.$route.name
    };
  },
  methods: {
    changeMenu(name) {
      this.$router.push({ name: name });
    },
  },
  mounted() {
    this.$Message.config({
      top: 100,
      duration: 3
    });
  },
  watch: {
    $route(to, from) {
      if (this.$route.meta.index == "tuoguan") {
        this.selectMenu = "公号托管";
      } else if (this.$route.meta.index == "msgView") {
        this.selectMenu = "消息管理";
      } else if (this.$route.meta.index == "groupSending") {
        this.selectMenu = "群发列表";
      }
    }
  }
};
</script>


