<template>
  <div class="slider">
    <Col span="8" class="slider-bar">
      <Menu theme="light" :active-name="sliderRoute" @on-select="changeSlider">
        <MenuItem v-for="sliderItem in sliderList" :name="sliderItem.name">
          <Icon :type="sliderItem.type"/>
          {{sliderItem.title}}
        </MenuItem>
      </Menu>
    </Col>
  </div>
</template>

<script>
export default {
  props: {
    sliderList: Array
  },
  data() {
    return {
      sliderRoute: ""
    };
  },
  mounted() {
    this.judge();
  },
  methods: {
    changeSlider(name) {
      this.$router.push({ name: name });
    },
    judge() {
      switch (this.$route.meta.content) {
        case "tuoguan":
          this.sliderRoute = "公号托管";
          break;
        case "menu":
          this.sliderRoute = "配置菜单";
          break;
        case "message":
          this.sliderRoute = "回复消息";
          break;
        case "guanli":
          this.sliderRoute = "消息管理";
          break;
        default:
          this.sliderRoute = "群发列表";
          break;
      }
    }
  },
  watch: {
    $route(to, from) {
      this.judge();
    }
  }
};
</script>

<style scoped>
.slider {
  height: 100%;
}
.slider-bar {
  position: fixed;
  height: 100%;
  border-right: 1px solid #dbdee2;
  width: 200px;
}
.slider-bar ul {
  margin-left: -40px;
}
</style>
