<template>
    <div class="login">
        <Form ref="loginRule" :model="loginForm" :rules="loginRule" class="login-form">
            <FormItem prop="username">
                <Input size="large" autocomplete="on" type="text" v-model="loginForm.username" placeholder="用户名">
                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                </Input>
            </FormItem>
            <FormItem prop="password">
                <Input size="large" type="password" v-model="loginForm.password" placeholder="密码">
                    <Icon type="ios-lock-outline" slot="prepend"></Icon>
                </Input>
            </FormItem>
            <FormItem>
                <Button size="large" type="primary" @click="onSubmit('loginRule')">登录</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
import * as types from '../types'
export default {
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      },
      loginRule: {
        username: [
          {
            required: true,
            message: "用户名不能为空！！",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "密码不能为空！！",
            trigger: "blur"
          }]
      }
    };
  },
  methods: {
    onSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid && this.loginForm.username == 'admin' && this.loginForm.password == '123456') {
          this.$Message.success("登录成功!");
          // if (this.token) {
            this.$store.commit(types.LOGIN, '123123321');
            let redirect = decodeURIComponent(this.$route.query.redirect || '/manage');
            this.$router.push({
              path: redirect
            })
          // }
        } else {
          this.$Message.error("Fail!");
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  height: 100vh;
  background: url("../assets/images/bg.jpg") no-repeat center;
  background-size: cover;
}

.login-form {
  width: 300px;
  margin: auto;
  padding-top: 200px;
}
</style>
